import React from 'react';
import { AlertColor } from '@mui/material/Alert/Alert';

export const LangPriorities = ['EN', 'UA', 'ES', 'RU'] as const;

export const PersonTypes = [
  'Owner',
  'OwnerDriver',
  'Coordinator',
  'CoordinatorDriver',
  'Driver',
] as const;

export const UserRoles = ['Admin', 'Super Admin'] as const;

export const TruckTypes = [
  'Cargo van',
  'Reefer van',
  'Box truck',
  'Box truck Reefer',
  'Straight truck',
  'Hotshot',
  'Tented box',
] as const;

export const TruckStatuses = ['Available', 'Not Available'] as const;

export const TruckCrossborders = ['Yes', 'No'] as const;

export const TruckCertificates = [
  'Hazmat',
  'Tsa',
  'TWIC',
  'Tanker Endorsement',
] as const;

export const TruckEquipments = [
  'Dock height risers',
  'Air ride',
  'Lift gate',
  'Keep from freezing',
  'ICC bar',
  'Vertical E-track',
  'Horizontal E-track',
  'Pallet jack',
  'PPE',
  'Ramps',
  'Straps',
  'Loads bars',
  'Blankets',
  'Pads',
  'Fire extinguisher',
] as const;

export type Order = 'asc' | 'desc';
export type Query<T> = {
  queryKey: [
    key: string,
    page: number,
    rowsPerPage: number,
    orderBy?: string,
    order?: Order,
    queryParams?: T,
  ];
};
export type QuerySingleKey = readonly [key: string, recordId: string];
export type QuerySingle = {
  readonly queryKey: QuerySingleKey;
};
export type Result<T> = {
  items: T[];
  limit: number;
  offset: number;
  total: number;
};
export interface AuthContextType {
  user: any;
  signin: (
    username: string,
    password: string,
    callback: (res: boolean) => void,
  ) => void;
  signout: (callback: VoidFunction) => void;
}
export type SnackbarMessage = {
  message: string;
  severity: AlertColor;
  handleClose?: (event?: React.SyntheticEvent | Event, reason?: string) => void;
};
export type PersonType = (typeof PersonTypes)[number];
export type LangPriority = (typeof LangPriorities)[number];
export type UserRole = (typeof UserRoles)[number];
export type TruckType = (typeof TruckTypes)[number];
export type TruckStatus = (typeof TruckStatuses)[number];
export type TruckCrossborder = (typeof TruckCrossborders)[number];
export type TruckCertificate = (typeof TruckCertificates)[number];
export type TruckEquipment = (typeof TruckEquipments)[number];

export interface City {
  id: string;
  zipCode: string;
  name: string;
  stateCode: string;
  stateName: string;
  location: [number, number];
}

export interface Person {
  id: string;
  fullName: string;
  type: PersonType;
  phone?: string;
}

export interface Owner extends Person {
  type: PersonType;
  phone: string;
  phone2?: string;
  email: string;
  address: string;
  ownTrucks?: Truck[];
  coordinators?: Coordinator[];
  drivers?: Driver[];
}

export interface Coordinator extends Person {
  type: PersonType;
  phone: string;
  phone2?: string;
  email: string;
  address: string;
  coordinateTrucks?: Truck[];
}

export interface Driver extends Person {
  type: PersonType;
  phone: string;
  phone2?: string;
  email?: string;
  address?: string;
  driveTrucks?: Truck[];
}

export interface Truck {
  id: string;
  truckNumber: number;
  status: string;
  lastLocation?: [number, number];
  lastCity?: City;
  locationUpdatedAt?: string;
  milesHaversine?: number;
  milesByRoads?: number;
  crossborder: string;
  certificate: string;
  type?: string;
  equipment?: string[];
  payload?: number;
  grossWeight: string;
  make: string;
  model: string;
  year: number;
  color: string;
  vinCode: string;
  licencePlate: string;
  licenceState: string;
  insideDims: string;
  doorDims?: string;
  validDims: string;
  owner?: Person;
  coordinator?: Person;
  driver?: Person;
}

export interface Load {
  id: string;
  loadNumber: number;
  pick?: City;
  pickDate?: Date;
  deliver?: City;
  deliverDate?: Date;
  milesByRoads?: number;
  milesHaversine?: number;
  weight: string;
  truckType: TruckType[];
  rate?: number;
  bookedByUser?: Person;
  bookedByCompany?: string;
  dispatchers?: Person[];
  checkInAs?: string;
  truck?: Truck;
}

export interface Location {
  id: string;
  zipCode: string;
  name: string;
  stateCode: string;
  stateName: string;
  location: [number, number];
}

export interface CreateLocation extends Omit<Location, 'id'> {
  password?: string;
}

export interface UpdateLocation extends Partial<CreateLocation> {
  id: string;
}

export interface User {
  id: string;
  fullName: string;
  phone?: string;
  userRole: UserRole;
  jobTitle: string;
  email: string;
}

export interface CreateUser extends Omit<User, 'id'> {
  password?: string;
}

export interface UpdateUser extends Partial<CreateUser> {
  id: string;
}
