import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import FormHelperText from '@mui/material/FormHelperText';
import Select from '@mui/material/Select';

import Loader from '../components/Loader';

import { UserUpdate, UserQuery } from '../api';
import { useSnackbar } from '../hooks';
import { UpdateUser, UserRoles } from '../utils/dataTypes';

const validationSchema = yup.object({
  fullName: yup.string().required('Full name is required'),
  phone: yup.string(),
  userRole: yup.string().oneOf(UserRoles).required('User role is required'),
  jobTitle: yup.string().required('Job title is required'),
  email: yup
    .string()
    .email('Enter a valid email')
    .required('Email is required'),
  password: yup
    .string()
    .min(8, 'Password should be of minimum 8 characters length'),
});

export default function EditUserForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['user', pathname.split('/')[3]],
    queryFn: UserQuery,
    keepPreviousData: true,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const { mutate, isLoading } = useMutation(UserUpdate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Saved',
      });
      queryClient.invalidateQueries(['users']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateUser>({
    initialValues: {
      id: '',
      fullName: '',
      phone: undefined,
      userRole: 'Admin',
      jobTitle: '',
      email: '',
      // password: '',
      ...data,
    },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as UpdateUser);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <Box key="fullName" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="fullName"
                  name="fullName"
                  label="Full Name"
                  value={formik.values.fullName}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.fullName && Boolean(formik.errors.fullName)
                  }
                  helperText={formik.touched.fullName && formik.errors.fullName}
                />
              </Box>
              <Box key="userRole" sx={{ height: '75px' }}>
                <FormControl
                  fullWidth
                  size="small"
                  error={
                    formik.touched.userRole && Boolean(formik.errors.userRole)
                  }
                >
                  <InputLabel id="userRole">User Role</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="userRole"
                    name="userRole"
                    label="User Role"
                    value={formik.values.userRole}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                  >
                    {UserRoles.map((role) => (
                      <MenuItem key={role} value={role}>
                        {role}
                      </MenuItem>
                    ))}
                  </Select>
                  <FormHelperText>
                    {formik.touched.userRole && formik.errors.userRole}
                  </FormHelperText>
                </FormControl>
              </Box>
              <Box key="jobTitle" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="jobTitle"
                  name="jobTitle"
                  label="Job Title"
                  value={formik.values.jobTitle}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.jobTitle && Boolean(formik.errors.jobTitle)
                  }
                  helperText={formik.touched.jobTitle && formik.errors.jobTitle}
                />
              </Box>
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%', height: '75px' }}>
              <Box key="phone" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="phone"
                  name="phone"
                  label="Phone"
                  value={formik.values.phone || ''}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={formik.touched.phone && Boolean(formik.errors.phone)}
                  helperText={formik.touched.phone && formik.errors.phone}
                />
              </Box>
              <Box key="email" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="email"
                  name="email"
                  label="Email"
                  value={formik.values.email}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={formik.touched.email && Boolean(formik.errors.email)}
                  helperText={formik.touched.email && formik.errors.email}
                />
              </Box>
              <Box key="password" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="password"
                  name="password"
                  label="Password"
                  value={formik.values.password}
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.password && Boolean(formik.errors.password)
                  }
                  helperText={formik.touched.password && formik.errors.password}
                />
              </Box>
            </Box>
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
                disabled={!formik.dirty}
              >
                Save
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading || loading || fetching} />
    </Box>
  );
}
